import React, { useRef, useState } from 'react'
import { css } from '@emotion/core'
import { Menu, Layout, Dropdown } from 'antd'
import { Icon } from 'Elements'

const { Header } = Layout

const SubMenu = Menu.SubMenu

const Headers = ({ setCollapsed, collapsed }) => {

  const userName = useRef(null)
  const [initialName] = useState('Super Admin')

  const getInitialName = () => {
    let parts = initialName.split(' ')
    let initials = ''
    for(let i = 0; i < parts.length; i++) {
      if(parts[i].length > 0 && parts[i] !== '') {
        initials += parts[i][0]
      }
    }
    return initials
  } 


  const getRandomColor = () => {
    let letters = '0123456789ABCDEF'
    let color = '#'
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)]
    }
    return color
  }

  const profilMenu = (
    <Menu>
      <Menu.Item key="profile">Profil</Menu.Item>
      <Menu.Item key="changeShift">Ganti Shift</Menu.Item>
      <Menu.Item key="logout">Keluar</Menu.Item>
    </Menu>
  )

  const notificationMenu = (
    <Menu>
      <Menu.Item key="profile">Not Found</Menu.Item>
    </Menu>
  )


  return (
    <Header className="header"
      css={css`
        display: grid;
        grid-template-columns: auto auto;
        background: #1890ff;
        box-shadow: 0px 3px 6px rgba(0,0,0,0.2); 
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 2;
        .ant-layout-header {
          line-height: 0;
        }
        @media(max-width: 700px){
          grid-template-columns: 1fr 2fr;
        }
      `}
    >
      <div css={css`display: flex;`}>
        <div>
          <Icon
            css={css`font-size: 1.5em; color: #fff;`}
            className="trigger"
            type={collapsed ? 'menu-unfold' : 'menu-fold'}
            onClick={() => setCollapsed(!collapsed)}
          />
        </div>
        <div className="logo">
          <h1 style={{color: '#fff'}}>Markir.id</h1>
        </div>
      </div>
      <div
        css={css`
          display: flex;
          align-content: center;
          justify-self: end;
          background: transparent;
          border: none;
        `}
      >
        <Dropdown overlay={notificationMenu} trigger={['click']}>
          <div css={css`
            color: #fff;
            padding: 0 1em;
            display: grid;
            cursor: pointer;
            align-content: center;
            margin-top: -10px;
            &:hover {
              color: rgba(255,255,255,0.7);
            }
            i {
              align-self: center;
              font-size: 2em;
            }
            @media(max-width: 700px){
              padding: 0 5px;
              margin-top: -7px;
              i {
                font-size: 1.5em;
              }
            }
          `}>
            <Icon name="notifications" />
          </div>
        </Dropdown>
        <Dropdown overlay={profilMenu} trigger={['click']}>
          <div css={css`
            display: flex; 
            align-items: baseline;
            color: #fff;
            cursor: pointer;
            padding: 0 5px; 
            &:hover {
              background: rgba(255,255,255,0.1);
            }
          `}>
            <div
              css={css`
                border-radius: 50%;
                overflow: hidden;
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                border: 1.5px solid #fff;
                font-size: 1.2em;
                font-weight: bold;
                background-color: ${getRandomColor()};
                @media(max-width: 700px){
                  width: 25px;
                  height: 25px;
                  font-size: 12px;
                }
              `}><label>{ getInitialName() }</label>
            </div>
            <div ref={userName} css={css`
              margin-left: 10px;
              @media(max-width: 700px){
                font-size: 12px;
                margin-left: 6px;
              }
            `}>{initialName}</div>
          </div>
        </Dropdown>
      </div>
    </Header>
  )
}

export default Headers